.body {
  background-color: black;
}

.Home {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-image: url(NightCity.jpg);
  background-position: center;
  background-size: cover;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  display: flex;
}

.Hero-image {
  position: fixed;
  display: flex;
  top: auto;
  left: auto;
  width: 42vmin;
  height: 42vmin;
  border-radius: 50%;
  cursor: pointer;
}

.Hero-loader {
  position: inherit;
  display: inherit;
  align-items: inherit;
  justify-content: inherit;

  top: auto;
  left: auto;

  border: 0.5em solid #000080;
  border-top: 0.5em solid black;
  border-bottom: 0.5em solid black;
  border-radius: 50%;

  width: 42vmin;
  height: 42vmin;
  animation: spin 30s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

.Menu {
  position: relative;
  width: 42vmin;
  height: 42vmin;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

/* Shared style for orbit buttons */
.orbit-button {
  display: block;
  position: absolute;
  width: 9vmin;
  height: 9vmin;
  padding: 2vmin;
  /* margin: -17.5%; */
  background: white;
  text-align: center;
  justify-content: center;
  align-items: center;

  /* Velikost tlacitek */
  width: 10vmin;
  height: 10vmin;

  text-align: center;

  /* Border effect */
  border: 0.5em solid black;
  border-top: 0.3em solid white;
  border-bottom: 0.3em solid white;
  border-radius: 51%;

  /* Orbiting */
  animation: orbit 30s linear infinite;
  /* radius */
  transform-origin: 20px 20px;
}

/* Positioning each button with different delays */
.orbit-1 {
  animation-delay: 0s;
}

.orbit-2 {
  animation-delay: -7.5s;
}

.orbit-3 {
  animation-delay: -15s;
}

.orbit-4 {
  animation-delay: -22.5s;
}

@keyframes orbit {
  from {
    transform: rotate(-180deg) translateX(37vmin) rotate(180deg);
  }
  to {
    transform: rotate(180deg) translateX(37vmin) rotate(-180deg);
  }
}


.ProjectsPage {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-color: black;
}


.ProjectsPage h1 {
  color: white;
}

.ProjectsPage p {
  color: white;
}

.AboutMePage {
  position: relative;
  width: 90%;
  height: 90%;
  height: 100vh;
  overflow-y: auto;
  overflow-y: scroll; /* enable scrolling */
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.AboutMePage::-webkit-scrollbar {
  display: none;  /* Chrome, Safari */
}
