.about-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem;
  font-family: "Segoe UI", sans-serif;
  color: #eaeaea;
  background-color: #0d1117; /* Dark navy/black */
  animation: fadeIn 1.5s ease;
  border-radius: 12px;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.6);
}

.about-header h1 {
  font-size: 2.8rem;
  margin-bottom: 0.2rem;
  color: #ffffff;
}

.about-subtitle {
  font-size: 1.2rem;
  color: #999;
  font-style: italic;
}

.about-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
  margin-top: 2rem;
}

.about-text p {
  line-height: 1.6;
  margin-bottom: 1rem;
  color: #ccc;
}

.about-photo .photo-placeholder {
  width: 100%;
  padding-top: 100%;
  background-color: #1c1c1c;
  border: 1px solid #333;
  border-radius: 8px;
  color: #666;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-skills {
  margin-top: 3rem;
}

.about-skills h2,
.about-timeline h2 {
  color: #ffffff;
}

.skills-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  margin-top: 1rem;
}

.skill-pill {
  background-color: #1a1a1a;
  color: #d4d4d4;
  padding: 0.5rem 1rem;
  border-radius: 999px;
  font-size: 0.95rem;
  transition: background 0.3s, transform 0.3s;
  border: 1px solid #333;
}

.skill-pill:hover {
  background-color: #007acc;
  color: white;
  transform: scale(1.05);
}

.about-timeline {
  margin-top: 3rem;
}

.timeline {
  list-style: none;
  padding: 0;
  color: #aaa;
}

.timeline li {
  margin-bottom: 1rem;
  position: relative;
  padding-left: 1.5rem;
}

.timeline li::before {
  content: "â€¢";
  position: absolute;
  left: 0;
  color: #007acc;
  font-weight: bold;
  font-size: 1.3rem;
  top: -0.1rem;
}

.about-footer {
  margin-top: 4rem;
  font-size: 1.1rem;
  color: #ccc;
  border-top: 1px solid #333;
  padding-top: 2rem;
  text-align: center;
  font-style: italic;
}

/* Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(16px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Mobile responsive */
@media (max-width: 768px) {
  .about-content {
    grid-template-columns: 1fr;
  }
}

.about-photo-img {
  width: 100%;
  max-width: 42vmin;
  border-radius: 8px;
  object-fit: cover;
  border: 2px solid #222;
  border-radius: 50%;
}

.tech-keyword {
  color: #60a5fa;
  transition: all 0.3s;
  position: relative;
}

.tech-keyword:hover::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -2px;
  height: 2px;
  width: 100%;
  background: #007acc;
}